<template>
  <div class="paly-vedio">
    <head-top title="播放记录">
      <span slot="right" class="head-right" @click="dataDrop">
        <van-icon name="delete" />
      </span>
    </head-top>
  </div>
</template>

<script>
import { Dialog } from "vant";
export default {
  data() {
    return {};
  },
  methods: {
    dataDrop() {
      Dialog.confirm({
        title: "提示",
        message: "是否删除此播放记录？"
      })
        .then(() => {})
        .catch(() => {});
    }
  }
};
</script>

<style lang="less" scoped>
.paly-vedio {
  background-color: #fff;
}
</style>


